const uni = require('uni').default;





































var dateUtils = require('../../common/util.js').dateUtils;

export default {
	data() {
		return {
			blockNum: 100000,
			imgUrls: [
				'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg',
				'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
				'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg'
			],
			walls: [{
				title: "表白墙",
				img: "../../static/c1.png"
			},{
				title: "表黑墙",
				img: "../../static/c1.png"
			},{
				title: "爱心墙",
				img: "../../static/c1.png"
			},{
				title: "大佬墙",
				img: "../../static/c1.png"
			},{
				title: "心得墙",
				img: "../../static/c1.png"
			},{
				title: "萌物墙",
				img: "../../static/c1.png"
			},{
				title: "商品墙",
				img: "../../static/c1.png"
			},{
				title: "互助墙",
				img: "../../static/c1.png"
			},{
				title: "问答墙",
				img: "../../static/c1.png"
			},{
				title: "通知墙",
				img: "../../static/c1.png"
			}],
			listData: []
		}
	},
	onShareAppMessage() {
		return {
			title: '欢迎体验uni-app',
			path: '/pages/component/component'
		}
	},
	onLoad() {
		this.getList();
	},
	methods: {
		trigerCollapse(e) {
			for (let i = 0, len = this.lists.length; i < len; ++i) {
				if (e === i) {
					this.lists[i].open = !this.lists[i].open;
				} else {
					this.lists[i].open = false;
				}
			}
		},
		getList() {
			var data = {
				column: "id,post_id,title,author_name,cover,published_at" //需要的字段名
			};
			console.log(this.last_id);
			if (this.last_id) { //说明已有数据，目前处于上拉加载
				data.minId = this.last_id;
				data.time = new Date().getTime() + "";
				data.pageSize = 10;
			}
			uni.request({
				url: 'https://spider.dcloud.net.cn/api/news',
				data: data,
				success: (data) => {
					if (data.statusCode == 200) {
						let list = this.setTime(data.data);
						this.listData = this.reload ? list : this.listData.concat(list);
						this.last_id = list[list.length - 1].id;
						this.reload = false;
					}
				},
				fail: (data, code) => {
					console.log('fail'+JSON.stringify(data));
				}
			})
		},
		setTime: function (items) {
			var newItems = [];
			items.forEach((e) => {
				newItems.push({
					author_name: e.author_name,
					cover: e.cover,
					id: e.id,
					post_id: e.post_id,
					published_at: dateUtils.format(e.published_at),
					title: e.title
				});
			});
			return newItems;
		}
	}
}



